
.btn-class.btn-pick::before {
  background: url("../../assets/icones_classes.png") 0 0 no-repeat;
}
.btn-class.btn-pick.hunter::before {
  background-position: -40px -40px;
}
.btn-class.btn-pick.warlock::before {
  background-position: -160px -40px;
}
.btn-class.btn-pick.shaman::before {
