}
.btn-class.btn-pick.mage::before {
  background-position: -40px -160px;
}
.btn-class.btn-pick.druid::before {
  background-position: -160px -160px;
}
.btn-class.btn-pick.paladin::before {
  background-position: -280px -160px;
}
.btn-class.btn-pick.priest::before {
  background-position: -40px -280px;
}
.btn-class.btn-pick.warrior::before {
  background-position: -160px -280px;
}
.btn-class.btn-pick.rogue::before {
  background-position: -280px -280px;
}



