/* ####### MAIN GLOBAL STYLE ####### */
/* #### MAIN COLORS #### */
/* Main Style */
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #454545;
  margin-top: 60px;
}

/* #### Specific Styles #### */
/* ####### Buttons ####### */
.btn {
  border-radius: 0px !important;
  position: relative;
  box-shadow: 0 3px #909090;
  margin-top: 4px;
  margin-bottom: 10px;
}
.btn:hover {
    transition: all 0.15s linear;
    box-shadow: 0 8px #909090;
    margin-top: 0px;
    margin-bottom: 4px;
}
.btn:focus {
    outline: none !important;
}
.btn:active {
    box-shadow: 0 1px #909090;
    outline: none;
    margin-top: 6px;
    margin-bottom: -3px;
    transition: all 0.05s linear;
}
.btn-success {
  border-color: #4CAE4C !important;
  box-shadow: 0 3px #3c8b3c;
}
.btn-success:hover {
    background-color: #4CAE4C !important;
    box-shadow: 0 6px #3c8b3c;
}
.btn-success:active {
    box-shadow: 0 2px #3c8b3c;
}
.btn-danger {
  border-color: #D43F3A !important;
  box-shadow: 0 3px #b42c27;
}
.btn-danger:hover {
    background-color: #D43F3A !important;
    box-shadow: 0 6px #b42c27;
}
.btn-danger:active {
    box-shadow: 0 2px #b42c27;
}
.btn-default {
  border-color: #D3921F !important;
  box-shadow: 0 3px #bd831c;
}
.btn-default:hover {
    background-color: #e3a942 !important;
    box-shadow: 0 6px #bd831c;
}
.btn-default:active {
    box-shadow: 0 2px #bd831c;
}
.btn-warning {
  border-color: #D3921F !important;
  box-shadow: 0 3px #bd831c;
}
.btn-warning:hover {
    background-color: #e09f2c !important;
    box-shadow: 0 6px #bd831c;
}
.btn-warning:active {
    box-shadow: 0 2px #bd831c;
}
.btn-class.btn-pick {
  position: relative;
  margin-left: 60px;
}
.btn-class.btn-pick:active:focus {
    outline: none;
}
.btn-class.btn-pick:focus {
    outline: none;
}
.btn-class.btn-pick::before {
    content: '';
    position: absolute;
    display: block;
    width: 80px;
    height: 80px;
    transition: 0.5s;
    border-radius: 50%;
    -webkit-transform: scale(0.65);
    transform: scale(0.65);
    left: -74px;
    top: -25px;
}
.duelButtons .btn-success {
  padding-right: 30px;
  position: relative;
}
.duelButtons .btn-success svg {
    position: absolute;
    top: 6px;
    right: 7px;
}
.duelButtons .btn-warning {
  padding-right: 30px;
  position: relative;
}
.duelButtons .btn-warning svg {
    position: absolute;
    top: 8px;
    right: 7px;
}

/* ####### Icons ####### */
.infoNote {
  margin-top: 5px;
  margin-left: 5px;
  position: absolute;
}
.chart {
  background-color: #463C3B;
  border-radius: 25px;
  padding: 20px;
  margin-bottom: 20px;
}
.chart h2 {
    color: #D3921F;
    border-bottom: 1px solid #D3921F;
    margin-top: 0;
    padding-bottom: 15px;
}
.hero {
  display: block;
  width: 150px;
  height: 150px;
  cursor: pointer;
  padding: 75px;
  /* IE 9 */
  -webkit-transform: scale(0.8);
  transform: scale(0.8);
  transition: 0.5s;
  border-radius: 50%;
  box-shadow: inset 0px 0px 1px 6px white, 1px 1px 8px 0 rgba(1, 1, 1, 0.5);
  /*box-shadow:3px 3px 15px 0 rgba(1,1,1,0.5);*/
}
.hero:hover {
  /* IE 9 */
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: 0.5s;
  box-shadow: inset 0px 0px 1px 6px white, 1px 1px 10px 0 rgba(1, 1, 1, 0.5);
}
.hero-label {
  cursor: pointer;
}

/* ** Deck Type Show ** */
.deckTypeContainer {
  margin-bottom: 15px;
}
.deckTypeContainer .deckTypeShow + div .btn-danger {
    padding-left: 30px;
}
.deckTypeContainer .deckTypeShow + div .btn-danger .fa-icon {
      position: absolute;
      left: 8px;
      top: 8px;
}
.deckTypeContainer .deckTypeShow {
    min-height: 120px;
    background: white;
    border: 1px solid black;
}
.deckTypeContainer .deckTypeShow .profil {
      width: 100%;
      height: 58px;
      display: inline-block;
}
.deckTypeContainer .deckTypeShow .profil h4 {
        color: white;
        top: 36px;
        z-index: 4;
        position: absolute;
        margin: 0 auto;
        text-align: center;
        width: 100%;
        min-width: 220px;
}
.deckTypeContainer .deckTypeShow .profil::after {
        content: '';
        width: 100%;
        height: 100%;
        /* ff3.6+ */
        /* safari4+,chrome */
        /* safari5.1+,chrome10+ */
        /* opera 11.10+ */
        /* ie10+ */
        background: linear-gradient(180deg, rgba(128, 128, 128, 0) 0%, black 100%);
        /* w3c */
        z-index: 2;
        position: absolute;
        top: 0;
        left: 0;
}
.deckTypeContainer .deckArchetype-aggro {
    box-shadow: inset 0px -6px 0px 0px #D43F3A;
}
.deckTypeContainer .deckArchetype-control {
    box-shadow: inset 0px -6px 0px 0px #089FA0;
}
.deckTypeContainer .deckArchetype-midrange {
    box-shadow: inset 0px -6px 0px 0px #4CAE4C;
}
.deckTypeContainer .deckArchetype-combo {
    box-shadow: inset 0px -6px 0px 0px #D3921F;
}
.deckTypeContainer .deckTypeShow .row {
    margin: 0;
}
.deckTypeContainer .deckTypeShow .row > div {
      padding: 0;
}
.deckTypeContainer .deckTypeShow .row .col-xs-2 {
      background-color: #000;
}
.deckTypeContainer .deckTypeShow .row .col-xs-2 > div {
        margin: 0 auto;
        position: relative;
        width: 30px;
        height: 58px;
}
.deckTypeContainer .deckTypeShow .editIcon,
  .deckTypeContainer .deckTypeShow .starIcon {
    cursor: pointer;
    padding-bottom: 1em;
}
.deckTypeContainer .deckTypeShow .editIcon:hover,
    .deckTypeContainer .deckTypeShow .starIcon:hover {
      padding-bottom: 0;
}
.deckTypeContainer .deckTypeShow .editIcon svg {
    position: absolute;
    top: 10px;
    left: 8px;
    transition: 0.3s ease;
}
.deckTypeContainer .deckTypeShow .editIcon:hover svg {
    top: 2px;
    left: 3px;
}
.deckTypeContainer .deckTypeShow .starIcon svg {
    position: absolute;
    top: 10px;
    right: 8px;
    transition: 0.3s ease;
}
.deckTypeContainer .deckTypeShow .starIcon:hover svg {
    top: 2px;
    right: 0;
}
.deckTypeContainer .deckTypeShow .stared {
    color: #D3921F;
}
.deckTypeContainer .deckTypeShow .noted {
    color: #D3921F;
}

/* ** Forms ** */
.settingForm {
  max-width: 600px;
  margin: 0 auto !important;
}
.settingForm .align-col {
    margin-bottom: 15px;
    text-align: left;
    margin-top: 15px;
}
.settingForm .starCheck {
    display: inline-block;
    margin-top: -8px;
}
.settingForm button {
    margin-top: -4px;
}
.settingForm button:hover {
      margin-top: -8px;
}
.settingForm button:active {
      margin-top: -1px;
}
.manageForm {
  max-width: 350px;
}

/* ** Archetype Pick ** */
.sweet-archetype .sweet-modal {
  max-width: 960px;
}
.sweet-archetype .sweet-modal .archetypePick {
    border: 1px solid transparent;
    cursor: pointer;
}
.sweet-archetype .sweet-modal .archetypePick :hover {
      border: 1px solid #D3921F;
}
.sweet-archetype .sweet-modal .archetypePick > div {
      padding: 20px 0 20px 75px;
      position: relative;
      text-align: left;
      color: black;
      font-weight: bold;
      margin: 15px 0;
      transition: 0.3s linear all;
}
.sweet-archetype .sweet-modal .archetypePick > div:hover {
        margin-top: 5px;
        transition: all 0.15s linear;
}
.sweet-archetype .sweet-modal .archetypePick > div:focus {
        outline: none;
}
.sweet-archetype .sweet-modal .archetypePick > div:active {
        transition: 0.3s linear all;
        margin-top: 24px;
        margin-bottom: 0;
        transition: all 0.15s linear;
}
.sweet-archetype .sweet-modal .archetypePick .aggro {
      background-color: #D43F3A;
      box-shadow: 0 10px #b42c27;
}
.sweet-archetype .sweet-modal .archetypePick .aggro::before {
        content: '';
        width: 60px;
        height: 60px;
        position: absolute;
        display: block;
        top: 12px;
        left: 20px;
}
.sweet-archetype .sweet-modal .archetypePick .aggro:hover {
        box-shadow: 0 20px #b42c27;
}
.sweet-archetype .sweet-modal .archetypePick .aggro:active {
        box-shadow: 0 2px #b42c27;
}
.sweet-archetype .sweet-modal .archetypePick .midrange {
      background-color: #4CAE4C;
      box-shadow: 0 10px #3c8b3c;
}
.sweet-archetype .sweet-modal .archetypePick .midrange::before {
        content: '';
        width: 60px;
        height: 60px;
        position: absolute;
        display: block;
        top: 12px;
        left: 20px;
}
.sweet-archetype .sweet-modal .archetypePick .midrange:hover {
        box-shadow: 0 20px #3c8b3c;
}
.sweet-archetype .sweet-modal .archetypePick .midrange:active {
        box-shadow: 0 2px #3c8b3c;
}
.sweet-archetype .sweet-modal .archetypePick .control {
      background-color: #089FA0;
      box-shadow: 0 10px #066f6f;
}
.sweet-archetype .sweet-modal .archetypePick .control::before {
        content: '';
        width: 57px;
        height: 80px;
        position: absolute;
        display: block;
        top: 2px;
        left: 20px;
}
.sweet-archetype .sweet-modal .archetypePick .control:hover {
        box-shadow: 0 20px #066f6f;
}
.sweet-archetype .sweet-modal .archetypePick .control:active {
        box-shadow: 0 2px #066f6f;
}
.sweet-archetype .sweet-modal .archetypePick .combo {
      background-color: #D3921F;
      box-shadow: 0 10px #a77318;
}
.sweet-archetype .sweet-modal .archetypePick .combo::before {
        content: '';
        width: 59px;
        height: 70px;
        position: absolute;
        display: block;
        top: 0;
        left: 20px;
}
.sweet-archetype .sweet-modal .archetypePick .combo:hover {
        box-shadow: 0 20px #a77318;
}
.sweet-archetype .sweet-modal .archetypePick .combo:active {
        box-shadow: 0 2px #a77318;
}

/* ####### Modals ####### */
.sweet-modal {
  text-align: center;
}
.sweet-modal .sweet-title > h2 {
    margin-top: 19px;
}
.sweet-modal .sweet-box-actions {
    top: 16px;
    width: 30px;
    right: 20px;
    height: 30px;
}
.sweet-modal .sweet-box-actions .sweet-action-close {
      width: 30px;
      height: 30px;
}
.sweet-modal .sweet-box-actions .sweet-action-close svg {
        margin-top: 3px;
}
.sweet-modal .sweet-box-actions .sweet-action-close:hover {
        width: 30px;
        height: 30px;
}
.hero {
  background: url("../../assets/classes_sprite.png") 0 0 no-repeat;
}
.hero-warrior {
  background-position: -375px -675px;
}
.hero-shaman {
  background-position: -675px -75px;
}
.hero-rogue {
  background-position: -675px -675px;
}
.hero-paladin {
  background-position: -675px -375px;
}
.hero-hunter {
  background-position: -75px -75px;
}
.hero-druid {
  background-position: -375px -375px;
}
.hero-warlock {
  background-position: -375px -75px;
}
.hero-mage {
  background-position: -75px -375px;
}
.hero-priest {
  background-position: -75px -675px;
}



