
.deckTypeContainer .deckTypeShow .profil {
  background: url("../../assets/charac_skins.jpg") 0 0 no-repeat;
}
.deckTypeContainer .deckClass-druid .profil {
  background-position: -32px 0;
}
.deckTypeContainer .deckClass-hunter .profil {
  background-position: -32px -70px;
}
.deckTypeContainer .deckClass-mage .profil {
  background-position: -32px -140px;
}
.deckTypeContainer .deckClass-paladin .profil {
  background-position: -32px -210px;
}
.deckTypeContainer .deckClass-priest .profil {
  background-position: -32px -280px;
}
.deckTypeContainer .deckClass-rogue .profil {
  background-position: -32px -350px;
}
.deckTypeContainer .deckClass-shaman .profil {
  background-position: -32px -420px;
}
.deckTypeContainer .deckClass-warlock .profil {
  background-position: -32px -490px;
}
.deckTypeContainer .deckClass-warrior .profil {
  background-position: -32px -560px;
}



